import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { PageHeader, Button, Table} from "antd";
import { ArrowLeftOutlined, ArrowRightOutlined } from "@ant-design/icons";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

import { Block } from "../constants/types";
import { getLatestBlocks } from "../apis/API";
import { relativeTime } from "../utils/utils";

const currentDate = new Date();
const timestamp = Math.floor(currentDate.getTime() / 1000);
const columns = [
  {
    title: "Block Index",
    dataIndex: "block_index",
    key: "block_index"
  },
  {
    title: "Hash",
    dataIndex: "hash",
    key: "hash",
    render: (text: string, record: Block) => (
      <Link to={"/" + record.hash}>{record.hash}</Link>
    )
  },
  {
    title: "Block Time",
    dataIndex: "time",
    key: "time"
  },
  {
    title: "Age",
    dataIndex: "age",
    key: "age",
    render: (text: string, record: Block) => (
      <p>{relativeTime(timestamp, parseInt(record.time))}</p>
    )
  },
  {
    title: "Block Height",
    dataIndex: "height",
    key: "height"
  }
];


const Blocks = (): JSX.Element => {
  const [blocks, setBlocks] = useState<Block[]>([]);
  const [pageNumber, setPageNumber] = useState<number>(1);
  const notifyError = () => toast("Please try again!");

  const incremnetPageNumber = () => {
    setPageNumber(pageNumber + 1);
  };

  const decrementPageNumber = () => {
    if( pageNumber>1){
      setPageNumber(pageNumber - 1);
    }
  };

  const fetchLatestBlocks = (): void => {
    getLatestBlocks(pageNumber)
      .then((data) => {
        console.log("In set state", JSON.stringify(data.data));
        // @ts-ignore
        setBlocks(data.data);
      })
      .catch((err: Error) => {
        console.log(err);
        notifyError();
      });
  };
  useEffect(() => {
    fetchLatestBlocks();
  }, [pageNumber]);
  return (
    <div>
      <ToastContainer />
      <PageHeader
        className="site-page-header"
        title={`Latest Blocks : Page ${pageNumber}`}
        subTitle={
          <div>
            <Button data-testid="increment-button" type="primary" onClick={incremnetPageNumber} icon={<ArrowRightOutlined />}>
              Next Page
            </Button>
            &nbsp;&nbsp;&nbsp;
            {pageNumber>1 &&  <Button data-testid="decrement-button"  type="primary" onClick={decrementPageNumber} icon={<ArrowLeftOutlined/>}>
              Previous Page
            </Button>}
          </div>

        }
      >

        <Table dataSource={blocks} columns={columns} />
      </PageHeader>
    </div>
  );
};

export default Blocks;
