import React, { useState } from "react";

import { addBlock } from "../apis/API";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

type Props = {
  saveTodo: (e: React.FormEvent, formData: AddBlock | any) => void
}

const AddBlock: React.FC<Props> = () => {
  const [formData, setFormData] = useState<AddBlock | {}>();
  const notify = () => toast("Successfully saved!");
  const notifyError = () => toast("Please try again!");

  const handleForm = (e: React.FormEvent<HTMLInputElement>): void => {
    // @ts-ignore
    setFormData({
      ...formData,
      [e.currentTarget.id]: e.currentTarget.value
    });
  };

  const handleSaveBlock = (e: React.FormEvent, formData: AddBlock | {} | any): void => {
    e.preventDefault();
    addBlock(formData)
      .then(() => {
        notify();
        setFormData({});

      })
      .catch((err) => {
        console.log(err);
        notifyError();
      });
  };

  // @ts-ignore
  return (
    <div>
      <ToastContainer />
      <form className="Form" onSubmit={(e) => handleSaveBlock(e, formData)}>
        <div>
          <div>
            <label className="fixedWidthLabel" htmlFor="name">Hash</label>
            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            <input onChange={handleForm} type="text" id="hash" />
          </div>
          &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
          <div>
            <label htmlFor="time">Block time</label>
            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            <input onChange={handleForm} type="number" id="time" />
          </div>
          &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
          <div>
            <label htmlFor="height">Block Height</label>
            &nbsp;&nbsp;&nbsp;&nbsp;
            <input onChange={handleForm} type="number" id="height" />
          </div>
          &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
          <div>
            <label htmlFor="block_index">Block Index</label>
            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            <input onChange={handleForm} type="number" id="block_index" />
          </div>
        </div>
        &nbsp;
        &nbsp;
        <div>
          <button disabled={formData === undefined}>Add Block</button>
        </div>

      </form>
    </div>

  );
};

export default AddBlock;
