import { Layout, Menu, Breadcrumb } from "antd";
import { BrowserRouter as Router, Route, Link, Switch } from "react-router-dom";

import Blocks from "./pages/Blocks";
import Help from "./pages/Help";
import AddBlock from "./pages/AddBlock";
import DetailedBlock from "./pages/DetailedBlock";
import MyBlocks from "./pages/MyBlocks";
import "antd/dist/antd.css";
import "./app.css";

const { Header, Content, Footer } = Layout;

const App = (): JSX.Element => {

  return (
    <Router>
      <Layout className="layout">
        <Header>
          <Menu theme="dark" mode="horizontal">

            <Menu.Item id="latest-blocks" key="blocks">
              <Link to="/">Latest Blocks</Link>
            </Menu.Item>

            <Menu.Item id="myBlocks" key="myBlocks">
              <Link to="/myBlocks">My Blocks</Link>
            </Menu.Item>

            <Menu.Item key="add">
              <Link to="/add">Add My Block</Link>
            </Menu.Item>

            <Menu.Item key="help">
              <Link to="/help">Help</Link>
            </Menu.Item>
          </Menu>
        </Header>
        <Content style={{ padding: "0 50px" }}>
          <Breadcrumb style={{ margin: "16px 0" }}>
            <Breadcrumb.Item>Home</Breadcrumb.Item>
            <Breadcrumb.Item>Blocks</Breadcrumb.Item>
          </Breadcrumb>
          <div className="site-layout-content">
            <Switch>
              <Route exact path="/" component={Blocks} />
              <Route path="/add" component={AddBlock} />
              <Route path="/help" component={Help} />
              <Route path="/myBlocks" component={MyBlocks} />
              <Route exact path="/:id" component={DetailedBlock} />
            </Switch>
          </div>
        </Content>
        <Footer style={{ textAlign: "center" }}>Dileka 2022 Madushan</Footer>
      </Layout>
    </Router>

  );
};

export default App;
