import { PageHeader, Table } from "antd";
import React, { useEffect, useState } from "react";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { Block } from "../constants/types";
import { getMyBlocks } from "../apis/API";

const columns = [
  {
    title: "Hash",
    dataIndex: "hash",
    key: "hash"
  },
  {
    title: "Block Time",
    dataIndex: "time",
    key: "time"
  },

  {
    title: "Block Height",
    dataIndex: "height",
    key: "height"
  },

  {
    title: "Block Index",
    dataIndex: "block_index",
    key: "block_index"
  }
];

type Props = any
const MyBlocks: React.FC<Props> = () => {
  const [blocks, setBlocks] = useState<Block[]>([]);

  const notifyError = () => toast("Please try again!");

  const fetchLatestBlocks = (): void => {
    getMyBlocks()
      .then((data) => {
        // @ts-ignore
        setBlocks(data.data);
      })
      .catch((err: Error) => {
        console.log(err);
        notifyError();
      });
  };
  useEffect(() => {
    fetchLatestBlocks();
  }, []);
  return (
    <div>
      <ToastContainer />
      <PageHeader
        className="site-page-header"
        title="My Blocks"
      >

        <Table dataSource={blocks} columns={columns} />
      </PageHeader>
    </div>
  );
};

export default MyBlocks;
