import { PageHeader, List, Typography, Spin, Alert } from "antd";
import { Block } from "../constants/types";
import { getOneBlock } from "../apis/API";
import React, { useEffect, useState } from "react";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

import { transformData, transformTransactions } from "../utils/utils";

const DetailedBlock = (props: any): JSX.Element => {
  const [block, setBlock] = useState<Block[]>([]);
  const [transactions, setTransactions] = useState<any[]>([]);
  const [isLoading, setIsLoading] = useState<boolean>(true);

  const notifyError = () => toast("Please try again!");

  const fetchOneBlock = (id: string): void => {
    getOneBlock(id)
      .then((data) => {
        // @ts-ignore
        setBlock(transformData(data.data));
        // @ts-ignore
        setTransactions(transformTransactions(data.data));
        setIsLoading(false);
      })
      .catch((err: Error) => {
        console.log(err);
        notifyError();
      });
  };
  useEffect(() => {
    fetchOneBlock(props.match.params.id);
  }, []);
  return (
    <div>
      <ToastContainer />
      {isLoading && <div className="example">
        <Spin size={"large"} tip={"Loading"}>
          <Alert
            message="Please wait"
            description="Checking the cache"
            type="info"
          />
        </Spin>
      </div>}
      {!isLoading &&
        <div>
          <PageHeader
            className="site-page-header"
            title="Properties of block"
          >
            <List
              bordered
              dataSource={block}
              renderItem={item => (
                <List.Item>
                  <Typography.Text mark /> {item}
                </List.Item>
              )}
            />
          </PageHeader>
          <PageHeader
            className="site-page-header"
            title="First 10 Transactions( values seperated by | )"
          >
            <List
              bordered
              dataSource={transactions}
              renderItem={item => (
                <List.Item>
                  <Typography.Text mark /> {item}
                </List.Item>
              )}
            />
          </PageHeader>
        </div>
      }

    </div>
  );
};

export default DetailedBlock;
