import axios, { AxiosResponse } from "axios";

//const baseUrl: string = 'http://localhost:3000'
const baseUrl: string = "https://xkdz8knq98.execute-api.us-east-1.amazonaws.com/dev";

// @ts-ignore
export const getLatestBlocks = async (pageNumber: number): Promise<AxiosResponse<ApiDataType>> => {
  try {
    const todos: any = await axios.get(
      `${baseUrl}/latestBlocks/${pageNumber}`
    );
    console.log("Data", todos.data);
    return todos.data;
  } catch (error) {
    console.log("error in retriving jsons");
  }
};

